.testimonialContainer {
  background-color: var(--blue);
  box-shadow: 0px 0px 12px -2px rgba(0, 0, 0, 0.3),
    0px 15px 12px 0px rgba(0, 0, 0, 0.22);
  width: 100%;
  position: relative;
  height: 400px;
  padding: 10% 0;
}

.testimonial {
/* background-color: aliceblue; */
  /* height: 700px; */
  width: 60%;
  margin: 0 auto;
  position: relative;

  overflow: hidden;
}
.testimonialRow {
  /* position: absolute;
  width: 75%; */
  display: flex;
  align-items: center;
  justify-content: center;
}
.testimonialImage {
  /* background-color: rgb(99, 89, 89); */
  flex: 30%;
text-align: center;
  /* position: absolute;
  top: 5%;
  z-index: 1;
  right: 1%; */
  /* width: 70%;
  height: 80%; */
 
}

.testimonialImage img {

  border-radius: 50%;
  width: 70%;
  height: 20%;
  object-fit: cover;
  border-radius: 50%;
   box-shadow: 9px -16px 0px 0px rgba(235, 203, 42, 0.178),
    0px 15px 12px 0px rgba(0, 0, 0, 0.16);
}
.voices{
  flex: 70%;
  /* background-color: rgb(220, 45, 45); */
}

.voices h3 {
  color: rgb(255, 255, 255);
  font-size: 1.4rem;
  font-weight: 500;
  font-family: "Cinzel", serif;
}
.voices h1 {
  color: rgb(238, 215, 151);
  font-size: 3rem;
  font-family: "Cinzel", serif;
  font-weight: 600;
}
.voices p {
  color: rgb(255, 255, 255);
  font-size: 1rem;
  font-family: "Montagu Slab", serif;
  line-height: 2;
}

.parents-name .testimonialLine {
  margin: 0 20px;
  height: 1rem;
  border-left: 2px solid var(--yellow);
}
.parents-name p {
  color: rgb(236, 143, 136);
  font-family: "Cinzel", serif;
}
.arrow {
  color: var(--blue);
  cursor: pointer;
  height: 2rem;
  width: 2rem;
  position: absolute;
}

.current {
  opacity: 1;
  transform: translateX(0);
}
.arrow {
  color: #777;
  cursor: pointer;
  position: absolute;
}
.arrow:hover {
  color: var(--yellow);
}
.next {
  top: 40%;
  right: 10%;
}
.prev {
  top: 40%;
 left: 10%;
}


@media screen and (max-width: 1024px) {
  .testimonialContainer{
    height: 700px;
   
  }
  .testimonialRow{
   flex-wrap: wrap;
   gap: 5rem;
  }
  .testimonialImage img {
    width: 40%;
    height: 20%;
  }
  .next {
    top: 50%;
    right: 10%;
  }
  .prev {
    top: 50%;
   left: 10%;
  }
}

@media screen and (max-width: 768px) {
  .testimonialContainer{
    height: 600px;
   
  }
  .testimonial{
    width: 80%;
  }
  
  .voices h1 {
    font-size: 2rem;
  }
  .voices h3 {
    font-size: 1rem;
  }
  .voices p {
    font-size: .8rem;
  }
 
  
}

